/* Image Divider Block */
.photo-divider {
  position: relative;
}

.photo-divider img {
  min-height: 200px;
  object-fit: cover;
  max-height: 55vh;
}

.photo-divider:before {
  position: absolute;
  content: "";
  display: block;
  background: rgba(255, 255, 255, 0.2);
  width: 100%;
  height: 100%;
}

/* Advert Block */
.image-container {
  position: relative;
  min-height: 400px;
}
.image-container img {
  position: absolute;
  object-fit: cover;
}

/* Quote Block */
.pull-quote-text {
  font-size: clamp(1.375rem, 0.8036rem + 0.8929vw, 1.875rem);
  font-style: italic;
  color: #06163E;
  font-weight: 300;
}

.pull-quote-name {
  font-size: clamp(1rem, 0.8571rem + 0.2232vw, 1.125rem);
  font-weight: 500;
  text-transform: capitalize;
  color: #06163E;
}

.pull-quote-block {
  display: flex;
  align-items: center;
}
.pull-quote-block .align-items-stretch .pull-quote-text,
.pull-quote-block .align-items-stretch .pull-quote-name {
