  margin: auto;
}

/* Testimonials Block */
.testimonial-slider-section .ti-widget-container,
.testimonial-slider-section .ti-widget.ti-goog > .ti-widget-container > .ti-header, .testimonial-slider-section .ti-widget.ti-goog > .ti-widget-container > .ti-footer {
