  font-weight: 300;
  font-style: italic;
  line-height: 1.6em;
  color: #06163E;
}

.testimonial-author {
  font-size: clamp(1.125rem, 0.993rem + 0.4225vw, 1.5rem);
  font-weight: 500;
  color: #06163E;
}

.slick-dots li {
  width: auto !important;
}

.slick-dots li button {
  border: 1px solid #BCAF9C !important;
  width: 17px !important;
  height: 17px !important;
  border-radius: 100%;
}
.slick-dots li button:before {
  display: none !important;
}

.slick-dots li:hover button {
  background-color: #BCAF9C !important;
}

.slick-dots li.slick-active button {
  background-color: #BCAF9C !important;
  width: 37px !important;
  border-radius: 30px !important;
}

button.slick-prev.slick-arrow,
button.slick-next.slick-arrow {
  border: 1.5px solid #BCAF9C !important;
  width: 45px;
  height: 45px;
  border-radius: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}
button.slick-prev.slick-arrow:hover,
button.slick-next.slick-arrow:hover {
  background-color: #BCAF9C !important;
  color: #fff !important;
}
@media screen and (max-width: 576px) {
  button.slick-prev.slick-arrow,
  button.slick-next.slick-arrow {
    display: none !important;
  }
}

.testimonial-slider .slick-prev:before, .testimonial-slider .slick-next:before {
  color: #BCAF9C !important;
}
.testimonial-slider .slick-prev {
  left: 0 !important;
}
.testimonial-slider .slick-prev::before {
  content: "\e905" !important;
  font-family: "icomoon" !important;
  opacity: 1 !important;
  padding-right: 3px;
}
.testimonial-slider .slick-prev:hover::before {
  color: #fff !important;
}
.testimonial-slider .slick-next {
  right: 0 !important;
}
.testimonial-slider .slick-next::before {
  content: "\e908" !important;
  font-family: "icomoon" !important;
  opacity: 1 !important;
  padding-left: 3px;
}
.testimonial-slider .slick-next:hover::before {
  color: #fff !important;
}
.testimonial-slider .testimonial-item i {
  position: absolute;
  font-size: clamp(1.875rem, 1.4349rem + 1.4085vw, 3.125rem);
  color: #06163E;
}
.testimonial-slider .testimonial-item {
  padding: 1rem 6rem;
}
.testimonial-slider i.icon-speech-mark-open {
  top: 0;
  left: 0;
}
.testimonial-slider i.icon-speech-mark-close {
  right: 0;
  bottom: 0;
}
.testimonial-slider ul.slick-dots {
  display: flex;
  justify-content: end;
  position: relative;
  bottom: 0;
  margin-top: 2rem;
}
.testimonial-slider .slick-slide {
  padding-left: 75px;
  padding-right: 75px;
}
@media (max-width: 767.98px) {
  .testimonial-slider .slick-slide {
    padding-left: 50px;
    padding-right: 50px;
  }
