    padding: 1rem;
  }
  .testimonial-slider .testimonial-item i {
    position: relative;
  }
  .testimonial-slider .icon-speech-mark-open {
    margin-bottom: 1rem;
    display: flex;
  }
  .testimonial-slider i.icon-speech-mark-close {
    margin-top: 1rem;
    display: flex;
    justify-content: end;
  }
}
