    padding-right: 0;
  }
}

.testimonial-slider-section h2.section-title, .testimonial-slider-section .section-title.h2 {
  padding: 0 30px;
}

/* Card Block */
.card-block .card img {
  border-radius: 0 !important;
  transition: transform 0.3s ease-in-out;
  object-fit: cover;
  height: 100%;
  width: 100%;
}
.card-block .card .card-img-wrapper {
  object-fit: cover;
  aspect-ratio: 1;
  max-height: 300px;
  background: white;
  overflow: hidden;
  position: relative;
}
.card-block .card .card-img-wrapper::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(255, 255, 255, 0.2);
}
.card-block .card .card-body {
  background-color: #D7ECF5;
  transition: background-color 0.3s ease-in-out;
}
.card-block .card:hover img {
  transform: scale(1.05);
  transition: transform 0.3s ease-in-out;
}
.card-block .card:hover .card-body {
  background-color: #F8F8F8;
  transition: background-color 0.3s ease-in-out;
}
.card-block .card-title {
  font-size: clamp(1.25rem, 1.0739rem + 0.5634vw, 1.75rem);
  color: #06163E;
}
.card-block a {
  text-decoration: none;
  color: #06163E;
}
.card-block .text-blurb-container {
  max-width: 1000px;
}

/* Table Block */
tr {
  border-left: 0;
  border-top: 0;
  border-right: 0;
  background: transparent;
  border-bottom: 1px solid #06163E;
}

td, th {
  border: none;
  background: 0 0;
  padding: 1rem 0.5rem !important;
  font-weight: 500;
  color: #06163E;
}

td {
  font-size: clamp(1rem, 0.956rem + 0.1408vw, 1.125rem);
}

/* Accordion Block */
.accordion-items__item__question {
  font-size: clamp(1.5rem, 1.3239rem + 0.5634vw, 2rem);
  padding: 1rem;
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 20px;
}

.accordion-items__item {
  border-bottom: solid 1px #06163E;
}

.accordion__answer {
  padding: 1rem 1rem 2rem;
}

.accordion-items__item__question {
  position: relative;
  cursor: pointer;
}

summary {
  list-style: none;
}

summary::-webkit-details-marker {
  display: none;
}

.accordion-items__item__question::after {
  content: "\e908" !important;
  font-family: "icomoon";
  border: 1.5px solid #bcaf9c !important;
  min-width: 45px;
  height: 45px;
  border-radius: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  right: 0;
  top: 50%;
  font-size: 20px;
  color: #BCAF9C;
  transform: rotate(90deg);
  transition: transform 0.3s ease;
}

/* Rotate arrow when <details> is open */
details[open] .accordion-items__item__question::after {
  transform: rotate(270deg);
  transition: transform 0.3s ease;
}

/* css override */
@media (min-width: 480px) and (max-width: 768px) {
  .col-xs-6 {
    flex: 0 0 auto;
    width: 50%;
